<template>
	<div class="ReplenishIndex">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>补货首页</span></header>
		<div class="container">
			<div class="select">
				<span>补货模式</span>
				<select ref="selectValue" value="">
					<option value="RN">RN-正常补货(补到满)</option>
					<option value="RL">RL-向下取整库位(少补)</option>
					<option value="RM">RM-向上取整库位(多补)</option>
				</select>
			</div>
			<span  class="g_singleBtn"   @click="toArea">确认</span>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"ReplenishIndex",
		data(){
			return{

			}
		},
		methods:{
			toArea:function(){

				console.log(this.$refs["selectValue"].value)

				this.$store.state.ReplenishMode = this.$refs["selectValue"].value;

				//跳转到 区域/库位 页面
				this.$router.push({
					name:"ReplenishArea"
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.ReplenishIndex{
		.container{
			overflow: hidden;
			&>div.select{
				width: 100%;
				height: 450/50rem;
				text-align: center;
				margin-top: 80/50rem;
				span{
					margin-right: 10/50rem;
				}
				select{
					width: 490/50rem;
	                height: 60/50rem;
	                padding: 0 10/50rem;
	                font-size: 29/50rem;
	                line-height: 60/50rem;
	                outline: none;
	                background-color:#fff;
	            }
			}
		}
	}
	@media screen and (max-width: 330px){
		.ReplenishIndex{
			.container{
				&>div.select{
					select{
		                height:70/50rem;
		                font-size: 35/50rem;
		                line-height:70/50rem;
		            }
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.ReplenishIndex{
			.container{
				&>div.select{
					span{
						margin-right: 0/50rem;
					}
					select{
						width: 510/50rem;
		                height: 90/50rem;
		                padding:0;
		                font-size: 40/50rem;
		                line-height: 90/50rem;
		            }
				}
			}
		}
	}
</style>


